.aboutContainer {
    height: 100vh;
    width: 100vw;
    display: flex;
    justify-content: center;
    align-items: center;


    .aboutMain {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        height: 80%;
        width: 60%;
        border-right: 5px solid #cac9ff;
        border-left: 5px solid #cac9ff;
        border-radius: 30px;
        padding: 30px;

        h1 {
            font-size: 70px;
        }
    }

    .descriptionContainer {
        align-items: center;
        justify-content: center;
        width: 50%;
        border-right: 2px solid white;
        letter-spacing: 2px;
        padding: 50px 20px 0px 0px;
        line-height: 2;
    }
}